<template>
    <div>
        <h3>新特性</h3>
        <ul>
            <li v-for="(item,index) in names.list" :key="index">{{item}}</li>
        </ul>
        <button @click="clickHandle">按钮</button>
        <p>{{msg}}</p>
        <p>父组件传递的数据：{{cunstomValue}}</p>
    </div>
</template>

<script>
import { ref,reactive, onMounted } from "vue";
import { inject } from "vue";

export default {
    name: "NewAttribute",
    // props 接收父组件传递的数据
    props: {
        msg : String, 
    },
    // 组合式 API
    setup(props, ctx) { 
        console.log('ctx: ' + ctx);
        // setup 中没有 this
        console.log(ctx);
        console.log(props.msg);
        const message = ref("消息");
        const names = reactive({
            list:['张三','李四','王五']
        });
        const msg = props.msg;
        function clickHandle() {
            // 通过 message.value 修改数据
            message.value = "按钮被点击了";
            names.list = ["小明", "小红", "小刚"];
        }

        onMounted(() => {
            console.log("mounted");
        });
        onMounted(() => {
            console.log("mounted2");
        });
        // 注入父组件的属性
        const cunstomValue = inject("cunstomValue");
        return {
            message,
            names,
            clickHandle,
            msg,
            cunstomValue
        }
    }
}
</script>