// import { get } from 'core-js/core/dict'
import { createStore } from 'vuex'
import axios from 'axios'

// Vuex 的核心作用就是帮我们管理组件之间的状态的
export default createStore({
    // 所有的状态都放在 state 中
    state: {
        counter: 10
    },
    getters: {
        // 相当于计算属性，依赖于 state
        getCounter(state) {
            return state.counter > 0 ? state.counter : 'counter 小于 0 不符合要求'
        }
    },
    // 同步操作，比如修改 state 的值，都放在 mutations 中
    mutations: {
        addCounter(state, num) { 
            state.counter += num
        },
    },
    // 异步操作，比如网络请求，都放在 actions 中
    // 可以包含任意异步操作，比如 setTimeout、ajax 请求等
    // 只允许存在异步操作
    actions: {
        asyncAddCounter({ commit }) { 
            axios.get('http://iwenwiki.com/api/generator/list.php')
            .then(res => {
                // 使用方式
                commit('addCounter', res.data[0])
            })
        }
    },
    modules: {
        
    }
})